import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
  //
  state = {
    isLoading: true,
    movies: [],
  };

  getMovies = async () => {
    const {
      data: {
        data: { movies },
      },
    } = await axios.get("http://localhost:3001/movies_list");
    this.setState({ movies, isLoading: false });
  };

  componentDidMount() {
    const movies = { movies: this.getMovies() };
  }

  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map(({ id, year, title, summary, poster, genres }) => (
              <Movie
                key={id}
                summary={summary}
                year={year}
                id={id}
                title={title}
                poster={poster}
                genres={genres}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default App;
